import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { TextToSpeech } from '@ionic-native/text-to-speech';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@IonicPage()
@Component({
  selector: 'page-result-of-a',
  templateUrl: 'result-of-a.html',
})
export class ResultOfAPage {

  a1: number;
  a2: number;
  res1Img: string;
  res2Img: string;
  result: string;
  stoped: boolean = false;
  data: string[][]; 


  constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, private tts: TextToSpeech) {
    this.a1 = navParams.get('a1');
    this.a2 = navParams.get('a2');
    this.http.get("assets/data/a01result.json").map(res => res.json()).subscribe(data => {
      this.data = data;
      this.result = this.data[this.a1 -1][this.a2 - 1];
    });
    this.res1Img = "assets/imgs/red" + this.a1 + ".png";
    this.res2Img = "assets/imgs/yellow" + this.a2 + ".png";   
  }

  ionViewDidLoad() {
    
    console.log('ionViewDidLoad ResultOfAPage');
  }

  speak(){
    this.stoped = !this.stoped;
    if (this.stoped)
    {
      this.tts.speak({
        text: this.result,
        rate: 0.8
      })
      .then(() => console.log('Success'))
      .catch((reason: any) => console.log(reason));
    }
    else{
      this.stop();
    }
    
  }

  stop()
  {
    this.tts.speak("");
  }

  goHome() {   
   this.navCtrl.popToRoot();
  }

  ngOnDestroy() {
    this.stop();
  }

}



