import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { A02Page } from '../a02/a02';

@IonicPage()
@Component({
  selector: 'page-a-1',
  templateUrl: 'a01.html',
})
export class A01Page {

  clicked: boolean = true;
  a1: number;
  imgSrc: any = [];
  public effect : any;
  public cssClass = [];
  

  constructor(public navCtrl: NavController, public navParams: NavParams) {
	  for(var i=0;i<12;i++)
	  {
		  this.imgSrc[i] = "assets/imgs/red.png";
	  }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad A01Page');
  }

  
  cantClick(effect : string,i : number) {
    if (this.clicked) {	  
      this.a1 = Math.floor(Math.random() * 12 + 1);
	  this.cssClass[i] = "animated " + effect;
	  setTimeout(()=>{
		  this.imgSrc[i] = "assets/imgs/red" + this.a1 + ".png";
	  },570);      
      this.clicked = false;
    }
  }

  goA02() {
    if (!this.clicked) {
      this.navCtrl.push(A02Page, {a1: this.a1});
    }
	else{
		for(var i=0;i<12;i++)
		{
			this.cssClass[i] = "animated " + "tada";  
		}
	}
  }

}



