import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { ResultOfAPage } from '../result-of-a/result-of-a';

/**
 * Generated class for the A02Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-a02',
  templateUrl: 'a02.html',
})
export class A02Page {

  clicked: boolean = true;
  a1: number;
  a2: number;
  imgSrc: any = [];
  public effect : any;
  public cssClass = [];

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.a1 = navParams.get('a1');
	for(var i=0;i<12;i++)
	  {
		  this.imgSrc[i] = "assets/imgs/yellow.png";
	  }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad A02Page');
  }

  cantClick(effect : string,i : number) {
    if (this.clicked) {
	  this.a2 = Math.floor(Math.random() * 12 + 1);
	  this.cssClass[i] = "animated " + effect;  
	  setTimeout(()=>{
		  this.imgSrc[i] = "assets/imgs/yellow" + this.a2 + ".png";
	  },570);      
      this.clicked = false;
    }
  }

  goResult() {
    if (!this.clicked) {
      this.navCtrl.push(ResultOfAPage,
        {
          a1: this.a1,
          a2: this.a2
        });
    }
	else{
		for(var i=0;i<12;i++)
		{
			this.cssClass[i] = "animated " + "tada";  
		}
	}
  }

}



